import React from 'react';
import styles from'./Navbar.module.css';
import {Link} from 'react-router-dom';
import {BsFillHouseFill} from "react-icons/bs";
import {BsBookHalf}from "react-icons/bs";
import {BsBarChartFill}from "react-icons/bs";

 
 function Navbar (){
     return (
         <nav >
            <h3 className={styles.logo}><BsBarChartFill  color="darkgreen" size="2.2rem"/> GradeAssist</h3>
            <ul>
               <Link className ={styles.linkstyle} to="/">
                  <li><BsFillHouseFill color="maroon" size="1.8rem" /> Home</li>
               </Link>
                <Link className={styles.linkstyle} to="/subjects">
                  <li><BsBookHalf color ="maroon" size="1.8rem" /> Subjects</li>
                </Link>
                
            </ul> 
         </nav>
     )
 }
 
 export default Navbar;
 
 
 
 